import logo from "../images/logo.svg";
import SContainer from "./Styled/SContainer";
import { SH1, SHeader, SLogo, SP1 } from "./Styled/Style";

function App({ username, count, ...props }) {
  const user = username.toString();

  return (
    <SContainer>
      <SHeader>
        <SLogo src={logo} alt="logo" />
        <SH1>React App</SH1>
        {user.length > 1 ? (
          <SP1>
            Ol {user}!  a sua {count} visita.
          </SP1>
        ) : (
          <SP1>Ol visitante! Por favor, faa login.</SP1>
        )}
      </SHeader>
    </SContainer>
  );
}

export default App;
